export class CellBuilderDrfFlag extends CellBuilderFlagIcon {

	iconWidth = ".8rem";

	iconFilter = "var(--filter)";

	protected _getIcon(row: IGridDataRow): string {
		const st = super._getValue(row);
		return st ? "--drf-" + st + "-url" : "";
	}

	protected _getValue(row: IGridDataRow): any {
		const st = super._getValue(row);
		return st ? WSP_DRF.getDrfStateLabel(st as EDrfState) : "";
	}
}

