/**
 *
 */
export class RevertSrc extends SrcAction<IShortDescCtx> {

	static SINGLETON = new RevertSrc();

	constructor() {
		super('revertSrc');
		this._group = "edit";
		this._icon = "/@skin@/wsp/actions/drfRevert.svg";
		this.atLeastOne = true;
		this.isNotRemoved = false;
		this._enableSrcPerms = ["action.drf#revert"];
	}

	getLabel(ctx: IShortDescCtx): string {
		if (ctx.shortDescs.length === 0) return "Abandonner toutes les modifications";
		if (ctx.shortDescs.every((sd) => sd.drfState === "erased")) return ctx.shortDescs.length === 1 ? "Annuler la suppression" : "Annuler les suppressions";
		return "Abandonner les modifications";
	}

	isEnabled(ctx: IShortDescCtx): boolean {
		if (ctx.shortDescs.some((sd) => {
			if (ITEM.getSrcUriType(sd.srcUri) !== "space") {
				const st = sd.drfState;
				return st !== "overriden" && st !== "erased" && st !== "notComputed";
			} else {
				return sd.drfState === "created";
			}
		})) return false;
		return super.isEnabled(ctx);
	}

	async execute(ctx: IShortDescCtx, ev?: Event) {
		const wsp = ctx.reg.env.wsp;
		if (await this.confirm(ctx, ev)) {
			await WSP_DRF.srcRevert(ctx.reg.env.wsp, ctx.emitter, ctx.shortDescs);
		}
	}

	confirm(ctx: IShortDescCtx, ev?: Event): Promise<boolean> {
		const count = ctx.shortDescs.length;
		if (count === 0) return POPUP.confirm("Confirmez l'abandon de toutes les modifications de cet atelier de travail. Le contenu original de l'atelier de référence sera restauré.", ctx.emitter, {okLbl: "Abandonner les modifications"});
		return POPUP.confirm(`Confirmez l'abandon de ces modifications. Le contenu original de l'atelier de référence sera restauré.`, ctx.emitter, {okLbl: "Abandonner les modifications"});
	}
}

REG.reg.registerSvc('drf/revertAction', 1, RevertSrc.SINGLETON);

/**
 *
 */
export class CommitSrc extends SrcAction<IShortDescCtx> {

	static SINGLETON = new CommitSrc();

	constructor() {
		super('commitSrc');
		this._group = "edit";
		this._icon = "/@skin@/wsp/actions/drfCommit.svg";
		this.atLeastOne = true;
		this.isNotRemoved = false;
		this._enableSrcPerms = ["action.drf#commit"];
	}

	getLabel(ctx: IShortDescCtx): string {
		if (ctx.shortDescs.length === 0) return "Valider toutes les modifications";
		if (ctx.shortDescs.every((sd) => sd.drfState === "erased")) return ctx.shortDescs.length === 1 ? "Valider la suppression" : "Valider les suppressions";
		return "Valider les modifications";
	}

	isEnabled(ctx: IShortDescCtx): boolean {
		if (ctx.shortDescs.some((sd) => {
			if (ITEM.isSrcUriInConflict(sd.srcUri)) return true;
			if (ITEM.getSrcUriType(sd.srcUri) !== "space") {
				const st = sd.drfState;
				return st !== "overriden" && st !== "created" && st !== "erased" && st !== "notComputed";
			} else {
				return false;
			}
		})) return false;
		return super.isEnabled(ctx);
	}

	async execute(ctx: IShortDescCtx, ev?: Event) {
		const wsp = ctx.reg.env.wsp;
		if (await this.confirm(ctx, ev)) {
			await WSP_DRF.srcCommit(ctx.reg.env.wsp, ctx.emitter, ctx.shortDescs);
		}
	}

	confirm(ctx: IShortDescCtx, ev?: Event): Promise<boolean> {
		const count = ctx.shortDescs.length;
		if (count === 0) return POPUP.confirm("Confirmez la validation de toutes les modifications de cet atelier de travail.", ctx.emitter, {okLbl: "Valider toutes les modifications"});
		if (count === 1) return POPUP.confirm("Confirmez la validation de cette modification.", ctx.emitter, {okLbl: "Valider la modification"});
		return POPUP.confirm(`Confirmez la validation de ces ${count} modifications.`, ctx.emitter, {okLbl: "Valider les modifications"});
	}
}

REG.reg.registerSvc('drf/commitAction', 1, CommitSrc.SINGLETON);

/**
 *
 */
export class RibbonDrf extends RibbonBase {

	protected _initialize(init: any): void {
		super._initialize(init);
		this.reg.env.longDescChange.add(this.refresh.bind(this));
		this.setTooltip(this._tooltip, true);
	}

	template(rib: RibbonDrf): TemplateResult {
		return xhtml`<div class="smallTi">Calque de travail</div><div id="st" data-drf-state="${rib.reg.env.longDesc.drfState}"><img id="ico"/>${WSP_DRF.getDrfStateLabel(rib.reg.env.longDesc.drfState)}</div>`
	}

	_tooltip(this: void, rib: RibbonDrf, tooltip: PopupTooltip): IView | null {
		return <div id="ttRoot">
			<BarActions class="vertical" î={{
				reg: rib.reg,
				actions: [rib.reg.getSvc('drf/revertAction'), rib.reg.getSvc('drf/commitAction')],
				actionContext: rib.shortDescCtx,
				uiContext: "dialog",
				disableFullOverlay: true
			} as OBarActionsInit<IShortDescCtx>}/>
		</div>;
	}

	protected _refresh() {
		if (!this.hidden) renderAppend(this.template(this), this.shadowRoot);
	}
}

