/**
 * Critère de recherche DRF : ItemCritDrfState
 *    <wsp-itemcrit-drf-state/>
 */
export class ItemCritDrfState extends UiCritBase {

	static AREA = new UiCritArea("wsp-itemcrit-drf-state").setLabel("État du calque de travail").setGroup("item")
		.setBuildAbstract((xml: Element, ctx: OUiCritInit): HTMLElement => {
			const statesCodesList = xml.hasAttribute("drfStates") ? xml.getAttribute("drfStates").split(" ") : [];
			const statesLabelsList: string[] = [];
			if (statesCodesList.indexOf("notOverriden") > -1) statesLabelsList.push("inchangé");
			if (statesCodesList.indexOf("overriden") > -1) statesLabelsList.push("modifié");
			if (statesCodesList.indexOf("created") > -1) statesLabelsList.push("créé");
			if (statesCodesList.indexOf("erased") > -1) statesLabelsList.push("supprimé");
			if (statesLabelsList.length > 0) {
				const statesTxt = statesLabelsList.join(" ou ");
				return <span class="crit">État du calque {statesTxt}</span>;
			}
		});


	protected _notOverridenBtn: ButtonToggle;

	protected _overridenBtn: ButtonToggle;

	protected _createdBtn: ButtonToggle;

	protected _erasedBtn: ButtonToggle;

	get notOverriden(): boolean {return this._notOverridenBtn.toggleOn}

	set notOverriden(state: boolean) {this._notOverridenBtn.toggleOn = state}

	get overriden(): boolean {return this._overridenBtn.toggleOn}

	set overriden(state: boolean) {this._overridenBtn.toggleOn = state}

	get created(): boolean {return this._createdBtn.toggleOn}

	set created(state: boolean) {this._createdBtn.toggleOn = state}

	get erased(): boolean {return this._erasedBtn.toggleOn}

	set erased(state: boolean) {this._erasedBtn.toggleOn = state}

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		const sr = this.shadowRoot;

		sr.appendChild(<div class="critHead">
			<span class="critTitle">État du calque de travail</span>
			{this.insertAlternativesBtn(init)}
		</div>);

		const btnsBox = sr.appendChild(<div class="btnsbox"/>);

		this._notOverridenBtn = btnsBox.appendChild(<ButtonToggle onclick={this.toggleButton} icon="/@skin@/wsp/objects/drfState/notOverriden.svg" label="Inchangés" class="notOverriden"
																															title="Items non surchargés dans cet atelier calque de travail" ui-context="dialog" aria-pressed="true"/>) as ButtonToggle;
		this._overridenBtn = btnsBox.appendChild(<ButtonToggle onclick={this.toggleButton} icon="/@skin@/wsp/objects/drfState/overriden.svg" label="Modifiés" class="icon overriden"
																													 title="Items surchargés dans cet atelier calque de travail" ui-context="dialog"/>) as ButtonToggle;
		this._createdBtn = btnsBox.appendChild(<ButtonToggle onclick={this.toggleButton} icon="/@skin@/wsp/objects/drfState/created.svg" label="Créés" class="created" title="Items créés dans cet atelier calque de travail"
																												 ui-context="dialog"/>) as ButtonToggle;
		this._erasedBtn = btnsBox.appendChild(<ButtonToggle onclick={this.toggleButton} icon="/@skin@/wsp/objects/drfState/erased.svg" label="Supprimés" class="erased" title="Items supprimés dans cet atelier calque de travail"
																												ui-context="dialog"/>) as ButtonToggle;
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		const states = xml.hasAttribute("drfStates") ? xml.getAttribute("drfStates").split(" ") : [];
		this.notOverriden = states.indexOf("notOverriden") > -1;
		this.overriden = states.indexOf("overriden") > -1;
		this.created = states.indexOf("created") > -1;
		this.erased = states.indexOf("erased") > -1;
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		const states: EDrfState[] = [];
		if (this.notOverriden) states.push("notOverriden");
		if (this.overriden) states.push("overriden");
		if (this.created) states.push("created");
		if (this.erased) states.push("erased");
		return new SearchDrfStates(states);
	}

	private toggleButton(this: ButtonToggle, ev?: Event): void {
		const me = DOMSH.findHost(this) as ItemCritDrfState;
		this.toggleOn = !this.toggleOn;
		me.onChange();
	}
}

REG.reg.registerSkin('wsp-itemcrit-drf-state', 1, /* language=CSS */ `
	.btnsbox {
		display: flex;
		flex-direction: row;
	}
`);

customElements.define("wsp-itemcrit-drf-state", ItemCritDrfState);

/**
 * Init du registre spécifique à un atelier de type DRF
 * 		init du registre **ui** (par app)
 */
export function initDrfReg(reg: IReg<IWspUiEnv>) {
	const drfColDef = new GridColDef("drf")
		.setFlex("1rem", 0, 0)
		.setCellBuilder(new CellBuilderDrfFlag("drfState"))
		.setSkin("wsp-explorer/grid/drf")
		.setLabel("")
		.setDescription("État du calque de travail");

	reg.addToList("columns:wspExplorer:secondaryCols", 'drf', 1, drfColDef, 10);
	reg.addToList("columns:netItems:secondaryCols", 'drf', 1, drfColDef, 10);

	reg.addToList("columns:wspApp:searches:item", "drf", 1, drfColDef);

	reg.addToList("columns:wspApp:searches-export:item", "drf", 1, new SearchExportColumn<IRegPointer<IWspUiEnv>>('drf').setColDef(
		new SearchFuncTranslateStr("drfState").setDict(() => {
			const states: { [key in EDrfState]: string } = {
				erased: WSP_DRF.getDrfStateLabel("erased"),
				notOverriden: WSP_DRF.getDrfStateLabel("notOverriden"),
				overriden: WSP_DRF.getDrfStateLabel("overriden"),
				created: WSP_DRF.getDrfStateLabel("created"),
				createdDeleted: WSP_DRF.getDrfStateLabel("createdDeleted"),
				notComputed: WSP_DRF.getDrfStateLabel("notComputed"),
			};
			return states;
		})).setLabel("Calque de travail : état").setExportStatus(ESearchExportColumnStatus.exportable));

	function addSrcSvcAction(reg: IReg<IWspUiEnv>, svc: string, sortKey?: number) {
		reg.addSvcToList("actions:wsp:shortDesc", svc, 1, svc, sortKey);
		// reg.addToList("actions:wspApp:explorer", act.getId(), 1, act, sortKey);
		// reg.addToList("actions:item:burger", act.getId(), 1, act, sortKey);
		// reg.addToList("actions:searchItem:shortDesc:item", act.getId(), 1, act, sortKey);
	}

	addSrcSvcAction(reg, 'drf/revertAction', -2);
	addSrcSvcAction(reg, 'drf/commitAction', -1);

	reg.addToList("ribbon:item", "drf", 1, RibbonDrf, 20);

	REG.reg.registerSvc("wsp-itemcrit-drf-state", 1, ItemCritDrfState.AREA);
	reg.addSvcToList("wsp.crit.items", "wsp-itemcrit-drf-state", 1, "wsp-itemcrit-drf-state");
}